* {
  box-sizing: border-box;
}

:root,
::before,
::after {
  box-sizing: inherit;
}

:root {

  /* 16 px  for 1440px*/
  font-size: calc(0.8rem + 0.23vw);
}

/* @media (min-width:1441px) {
  :root {
    font-size: 1em;
  }
} */


body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'SFUIDisplayNormal', sans-serif;
  /* font-size: inherit; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* iframe {
  display: none;
} */

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

button,
input,
label,
table {
  cursor: pointer;
}

button {
  transition: all .2s linear;
}


a {
  text-decoration: none;
  border: 1px solid transparent;
  transition: all .2s linear;
}

.App {
  text-align: center;
  /* background: linear-gradient(180deg, #E6D4DE 0%, #9890C7 100%); */
  background: url(/friday/static/media/bgGlobal.82244eac.jpg) center center no-repeat fixed;
  background-size: cover;
  min-height: 100vh;
  width: 100%;
  padding-bottom: .5rem;
  position: relative;
}

.App::before {
  position: absolute;
  display: block;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(47, 53, 59, .5);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
